.chat-box {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 100%;
    height: 75vh; /* Set an appropriate height */
    overflow-y: auto;
  }
  
  .chat-bubble {
    position: relative;
    display: flex;
    flex-direction: column;
    // min-width: 100%;
    padding: 10px;
    border-radius: 8px;
    box-sizing: border-box;
    margin: 5px;
  }
  
  .username {
    font-size: 12px;
  }
  
  .message {
    font-size: 16px;
  }
  
  .timestamp {
    font-size: 10px;
    color: gray;
    padding:0 50px;
  }
  
  .sender {
    background-color: rgb(38, 101, 143);
    color:white;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    align-self: flex-start;
    min-width: 20%;
    
    margin-left: 50px;
    max-width: 50%;
    border-radius: 0 10px 10px 10px;
    text-align: justify;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    }
  
  .receiver {
    background-color: #b8bdfc; /* Adjust background color for receiver message */
    color:black;
    display: flex;
    justify-content: flex-end;
    align-self: flex-end;
    align-items: flex-start;
    min-width: 20%;
    max-width: 50%;
    margin-right: 50px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    text-align: justify;
    
    border-radius: 10px 0 10px 10px;
}
  