.userList {
  position: relative;
    text-align: center;
    display:flex;
    justify-content: center;
    align-items: center;
    // margin-top: 45px !important;
    flex-direction: column;
    padding: 0 20px !important;
    box-sizing: border-box !important;
  
    &__list {
      // position: sticky;
      // align-self: flex-start;
      top: 45px;
      overflow-y: auto;
      // max-height: 500px;
      list-style-type: none;
      padding: 0;
      display:flex;
      flex-direction: column;
      min-height: 370px;
      min-width: 100%;
      // padding: 0 5%;
      justify-content: flex-start;
      align-items: center;
      background-color: rgb(181, 207, 255);
      border-radius: 5px;
      box-shadow: 0 1px 3px #000;
  
      li {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: row;
        width: 100%;
        min-width: 100px;
        cursor: pointer;
        min-height: 30px;
        // background-color: rgb(204, 99, 99);
        font-size: 18px;
        color: rgb(59, 55, 55);
        // border-radius: 4px;
        margin: 10px 0;
        // box-shadow: 0 1px 3px #777;
        transition: all 0.5s cubic-bezier(0.215, 0.610, 0.355, 1);

        &:hover{
          background-color: rgb(206, 191, 245);
        }
      }
    }

    .chatRoom{
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      min-height: 200px;
      margin: 30px 0 110px 0;
    }

    .messageInput{
      width: 100%;
    }

    .textField{
      position: fixed;
      bottom: 32px;
      max-width: 70vw;
      right: 30px;
      background-color: aliceblue;
      box-shadow: 0 1px 3px #333;
      border-radius: 5px;
    }

    span{
      line-height: 4px;
      font-size: 12px;
    }

    .currentUser{
      border-radius: 6px;
      box-shadow: 0 1px 3px #333;
      background-color: rgb(38, 101, 143);
      color:white;
    }
  }
  