.header{
    position: fixed;
    top:0;
    right:0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    height: 40px;
    color: rgb(226, 226, 226);
    // background-color: rgba(255, 255, 255, 0.15);
    
    background-color: rgb(148, 224, 97);
    box-shadow: 0 1px 3px #333;

    backdrop-filter: brightness(70%) blur(5px);
    z-index: 10;
    padding:0 20px;
    box-sizing: border-box;

    h5,span{
      font-family: 'Dancing Script', cursive;
      letter-spacing: 6px;
    }

    span{
      color: rgb(243, 8, 8);
    }

    h5{
      color: rgb(39, 39, 39);
      font-weight: 800;
    }

    .logo{
      display: flex;
      justify-content: flex-start;
      align-items: center;
    }
}