.signin {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 100px;
    min-height: 500px;  
  
    h2 {
      font-size: 24px;
      margin-bottom: 16px;
      
      font-family: Tahoma, sans-serif;
    }
  
    form {
      display: flex;
      flex-direction: column;
  
      label {
      font-family: Tahoma, sans-serif;
      }
  
      input {
        padding: 10px;
        direction: ltr;
        min-height: 30px;
        padding-left: 30px;
        
      font-family: Tahoma, sans-serif;
      }
  
      button {
        padding: 8px 16px;
        background-color: #c74c60;
        border: none;
        color: white;
        cursor: pointer;
        
      font-family: Tahoma, sans-serif;
  
        &:hover {
          background-color: #e45c68;
        }
      }
    }

    .toggle-link {
      color: blue;
      cursor: pointer;
      
      font-family: Tahoma, sans-serif;
    }
  
    .underLink {
      margin-top: 30px;

      font-family: Tahoma, sans-serif;
    }
  }
  